/* tree slect 分类选择器 */+/* dropdown 下拉菜单 */
<template>
  <div>
    <van-dropdown-menu active-color="">
        <!-- 区域下拉菜单 -->
      <van-dropdown-item v-model="value1" :title="areatitle" ref="price">
        <van-cell>
          <template>
            
            <van-tree-select @click-item="clickcountiesitem" :items="area" :main-active-index.sync="areaindex">
              <template #content>
                  
                <van-cell v-if="areaindex===0">
                    
                  <van-tree-select 
                  @click-nav="clickareaitem" 
                  :items="nearby" 
                  :main-active-index.sync="nearbyindex">
                  </van-tree-select>
              
                </van-cell>
                <van-cell v-if="areaindex===1">
                  <van-tree-select
                    @click-item="clickcountiesitem"
                    :items="counties"
                    :main-active-index.sync="countiesindex"
                    :active-id.sync="countiesId"
                  ></van-tree-select>
                </van-cell>
              </template>
            </van-tree-select>
          </template>
        </van-cell>
      </van-dropdown-item>
      <!-- 价格下拉菜单 -->
      <van-dropdown-item :title="pricetitle" @change="clickpriceitem" v-model="value1" :options="option1" />
      <!-- 户型下拉菜单 -->
      <van-dropdown-item :title="huxingtitle" @change="clickhuxingitem"  v-model="value3" :options="option3" />
      <!-- 筛选下拉菜单 -->
      <van-dropdown-item   v-model="value4" ref="item" :options="option4">
       <!-- <van-field name="radio" label="单选框">
          <template #input>
            <van-radio-group v-model="radio" direction="horizontal">
                <van-radio name="1">单选框 1</van-radio>
                <van-radio name="2">单选框 2</van-radio>
            </van-radio-group>
          </template>
        </van-field> -->
        <van-cell>
            <template>
                <van-radio v-for="item in list" :key="item.value">
                    {{item.text}}
                </van-radio>
            </template>
        </van-cell>

      </van-dropdown-item>
    </van-dropdown-menu>
  </div>
</template>
<script>
export default {
  name: "Topfilter",
  data() {
    return {
      radio: '1',
      areaindex: 0 /* 区域索引 */,
      areatitle:"区域",/* 区域主题 */
      nearbyindex: 0 /* 附近索引 */,
      countiesindex: 0 /* 县区索引 */,
      pricetitle:"价格",
      huxingtitle:"户型",
      areaId: "",
      countiesId: "" /* 县区id */,
      area: [
        {
          text: "附近"
        },
        {
          text: "区域"
        }
      ],
      nearby: [
        { text: "不限", id: 0 },
        { text: "1000米以内", id: 1 },
        { text: "2000米以内", id: 2 },
        { text: "3000米以内", id: 3 }
      ],
      counties: [
        {
          text: "不限",
          id: 4
        },
        {
          text: "东昌府区",
          id: 5,
          children: [
            {
              text: "不限",
              id: 6
            },
            {
              text: "城区",
              id: 7
            },
            {
              text: "其他",
              id: 8
            }
          ]
        },
        {
          text: "开发区",
          id: 9,
          children: [
            {
              text: "不限",
              id: 10
            },
            {
              text: "城区",
              id: 11
            },
            {
              text: "其他",
              id: 12
            }
          ]
        },
        {
          text: "高新区",
          id: 13,
          children: [
            {
              text: "不限",
              id: 14
            },
            {
              text: "城区",
              id: 15
            },
            {
              text: "其他",
              id: 16
            }
          ]
        },
        {
          text: "度假区",
          id: 17,
          children: [
            {
              text: "不限",
              id: 18
            },
            {
              text: "城区",
              id: 19
            },
            {
              text: "其他",
              id: 20
            }
          ]
        },
        {
          text: "东阿",
          id: 21,
          children: [
            {
              text: "不限",
              id: 22
            },
            {
              text: "城中心",
              id: 23
            },
            {
              text: "城南区",
              id: 24
            },
            {
              text: "城东区",
              id: 25
            },
            {
              text: "城西区",
              id: 26
            },
            {
              text: "城北区",
              id: 27
            }
          ]
        },
        {
          text: "临清",
          id: 28,
          children: [
            {
              text: "不限",
              id: 29
            },
            {
