            },
            {
              text: "城区",
              id: 34
            },
            {
              text: "其他",
              id: 35
            }
          ]
        },
        {
          text: "冠县",
          id: 36,
          children: [
            {
              text: "不限",
              id: 37
            },
            {
              text: "城区",
              id: 38
            },
            {
              text: "其他",
              id: 39
            }
          ]
        }
      ],

      active1: "",
      value1: 10,
      value3: 11,
      value4: 11,
      option1: [
        // { text: "价格", value: 10 },
        { text: "不限", value: 0 },
        { text: "5000以下", value: 1 },
        { text: "5000-6000", value: 2 },
        { text: "6000-7000", value: 3 },
        { text: "7000-8000", value: 4 },
        { text: "8000-9000", value: 5 },
        { text: "9000以上", value: 6 }
      ],

      option3: [
        { text: "不限", value: 0 },
        { text: "一居室", value: 1 },
        { text: "二居室", value: 2 },
        { text: "三居室", value: 3 },
        { text: "四居室", value: 4 },
        { text: "五居室", value: 5 },
        { text: "五居室以上", value: 6 }
      ],
      option4: [
        { text: "筛选", value: 11 },
        { text: "好评排序", value: "8" },
        { text: "销量排序", value: "9" },
      ],
      list:[
        { text: "户型", value: 11 },
        { text: "不限", value: 0 },
        { text: "一居室", value: 1 },
        { text: "二居室", value: 2 },
        { text: "三居室", value: 3 },
