        { text: "五居室以上", value: 6 }
      ]
    };
  },
  methods: {
      /* 当dropdownitem发生变化时触发 */
      change(a){
          console.log(a)
      },
      /* 点击附近事件 */
      clickareaitem(index){
          console.log(index)
          console.log(this.nearbyindex)  
          this.areatitle = this.nearby[index].text
      },
      /* 点击县区事件 */
      clickcountiesitem(a){
          console.log(a)
          this.areatitle=a.text 
        //   this.nearbyindex=0
          this.$refs.price.toggle();
      },
      clickpriceitem(a){
          this.pricetitle = this.option1[a].text
          if(a===0){
              this.pricetitle ="价格"
          }
        console.log(a)
      },
      clickhuxingitem(a){
          this.huxingtitle = this.option3[a].text
          if(a===0){
              this.huxingtitle ="户型"
          }
        console.log(a)
      }

  },
    watch: {
        nearbyindex(){
            this.$refs.price.toggle();
        }
    },
};
</script>
<style lang="scss" scoped>
.van-cell {
  padding: 0;
  width: 300px;
  background: #ffffff;
}
.van-tree-select__nav {
  background: #ffffff;
}
.van-sidebar-item {
  background: #ffffff;
}
</style>